@import url('https://fonts.googleapis.com/css?family=Comfortaa:700|Cormorant+Garamond:700|Open+Sans&subset=cyrillic');

/* font-family: 'Comfortaa', cursive;
font-family: 'Cormorant Garamond', serif;
font-family: 'Open Sans', sans-serif; */

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li,
ol li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

body {
  background-color: #9ee0ca;
  font-size: 20px;
  font-family: 'Comfortaa', cursive;
  color: #1f2d2e;
}

a {
  color: #1f2d2e;
  text-decoration: none;
}

a:hover,
a:active,
a:focus {
  transition: all 0.15s ease-in-out;
}

a:focus {
  outline-color: #1f2d2e;
}

button:focus {
  outline-color: #1f2d2e;
}

button:hover,
button:active {
  outline: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  margin-bottom: 80px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: inline-block;
  font-weight: 700;
  color: white;
  text-decoration: none;
  padding: 8px 20px;
  border-radius: 6px;
  background-color: #1f6c45;
  box-shadow: 0 -3px #195838 inset;
  transition: 0.2s;
}

.button:hover {
  background-color: #195838;
}

.button:active {
  background-color: #14442c;
  box-shadow: 0 -3px #0e311f inset;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: black;
  z-index: 2;
  opacity: 0.5;
}

.overlay__show {
  display: block;
  opacity: 0.5;
}

.overlay__hide {
  display: none;
  opacity: 0;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
}

.modal__hide {
  display: none;
}

.modal__show {
  display: block;
}

.header {
  font-size: 48px;
  margin-bottom: 50px;
}

.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 5px 0;
  background-color: #446655;
  color: #1f2d2e;
  z-index: 2;
}

.main-header--container {
  display: flex;
  justify-content: center;
}

.main-header--nav {
  width: calc(50% - 30px);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  margin-top: auto;
  margin-bottom: auto;
}

.main-header--list {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.main-header--item {
  display: inline-block;
}

.main-header--item__active .main-header--link {
  color: #d2f9f9;
}

.main-header--item:hover .main-header--link,
.main-header--item:focus .main-header--link {
  color: #d2f9f9;
}

.main-header--item:active .main-header--link {
  color: #d2f9f9;
}

.main-header--link {
  display: block;
  color: #1f2d2e;
}

.main-header--mobile {
  padding: 0;
  border: none;
  font-size: 0;
  background-color: transparent;
  display: none;
  margin: -5px 0;
}

.main-header--mobile::before {
  content: "≡";
  color: #d2f9f9;
  font-size: 40px;
}

.main-header--purchase {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.main-header--orders {
  display: flex;
}

.main-header--orders:hover .main-header--buy::before,
.main-header--orders:focus .main-header--buy::before,
.main-header--orders:active .main-header--buy::before {
  background-image: url(../img/cart-active.svg);
}

.main-header--orders:hover .main-header--amount,
.main-header--orders:focus .main-header--amount,
.main-header--orders:active .main-header--amount {
  color: #d2f9f9;
  border-color: #d2f9f9;
  background-color: rgba(210, 249, 249, 0.2);
}

.main-header--amount {
  display: inline-block;
  margin: auto 0 auto 8px;
  padding: 3px 9px;
  order: 1;
  background-color: #344f45;
  color: #1f2d2e;
  border: 2px solid #20312b;
  border-radius: 50%;
}

.main-header--buy {
  font-size: 0;
}

.main-header--buy::before {
  content: '';
  display: inline-block;
  width: 40px;
  height: 38px;
  background: url(../img/cart.svg) center no-repeat;
  background-size: cover;
}

.shop-nav {
  background: url(../../img/nav-bg.jpg) center no-repeat;
  background-size: cover;
  font-size: 28px;
  min-height: 100vh;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #d2f9f9;
}

.shop-nav--container {
  width: calc(66.66666666666667% - 30px);
  background-color: rgba(68, 102, 85, 0.49);
  border: 10px solid rgba(210, 249, 249, 0.24);
  text-align: center;
  padding: 30px 0;
}

.shop-nav--header {
  font-family: 'Cormorant Garamond', serif;
  font-size: 65px;
  letter-spacing: 10px;
  margin-bottom: 150px;
}

.shop-nav--list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.shop-nav--item {
  display: inline-block;
}

.shop-nav--link {
  color: #d2f9f9;
}

.shop-nav--link:hover,
.shop-nav--link:focus,
.shop-nav--link:active {
  color: #1b1d1d;
}

.shop {
  text-align: center;
}

.shop--article {
  margin-top: 50px;
}

.shop--article__backpacks {
  margin-top: 0;
}

.shop--list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: flex-start;
}

.shop--item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  margin-bottom: 30px;
}

.product {
  display: flex;
  flex-direction: column;
}

.product--img {
  position: absolute;
  opacity: 0;
  max-height: 200px;
  width: auto;
  transition: transform 0.4s ease-in-out;
}

.product--img:hover {
  transform: scale(1.5);
}

.product--img__active {
  position: static;
  opacity: 1;
  z-index: 1;
}

.product--more-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.product--more-info__img {
  min-height: 200px;
}

.product--about {
  flex-grow: 1;
}

.product--colors {
  margin-bottom: auto;
}

.product--color {
  font-size: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  width: 19px;
  height: 19px;
  border-radius: 50%;
  border: 2px solid #9ee0ca;
  background-color: red;
  margin-bottom: 10px;
  transition: border 0.2s ease-in-out;
}

.product--color:hover,
.product--color:active {
  box-sizing: border-box;
  border: 2px solid #faee01;
}

.product--color__active {
  box-sizing: border-box;
  border: 2px solid #faee01;
}

.product--about {
  margin-bottom: 5px;
}

.product--size {
  margin-bottom: 10px;
}

.product--price {
  margin-bottom: 15px;
  display: block;
  margin-top: auto;
  font-size: 27px;
  color: #028e72;
  font-style: italic;
  color: #A6603A;
}

.product--buy {
  align-self: center;
}

.red {
  background-color: #822722;
}

.beige {
  background-color: #f6f3a5;
}

.blue {
  background-color: #002157;
}

.dark-blue {
  background-color: #2e3343;
}

.pink {
  background-color: #e38a8a;
}

.sky-blue {
  background-color: #6ebac5;
}

.gray {
  background-color: #b0b0b0;
}

.burgundy {
  background-color: #601419;
}

.brown {
  background-color: #754c24;
}

.flesh {
  background-color: #b6996a;
}

.black {
  background-color: #171d1d;
}

.dark-brown {
  background-color: #592211;
}

.green {
  background-color: #006954;
}

.white {
  background-color: #FAEEDC;
}

.white-brown {
  background-color: #B79F82;
}

.mustard {
  background-color: #FDA859;
}

.violet {
  background-color: #bda7b3;
}

.dark-violet {
  background-color: #523f45;
}

.bordo {
  background-color: #270c1d;
}

.alt-nav {
  position: fixed;
  top: 50%;
  right: 2%;
  display: none;
}

.alt-nav__visible {
  display: block;
}

.alt-nav--item {
  text-align: right;
}

.alt-nav--item:hover .alt-nav--text {
  display: inline-block;
}

.alt-nav--item:hover .alt-nav--link::after {
  background-color: #1f2d2e;
}

.alt-nav--item__active .alt-nav--text {
  display: inline-block;
}

.alt-nav--item__active .alt-nav--link::after {
  background-color: #1f2d2e;
}

.alt-nav--link {
  font-style: italic;
}

.alt-nav--link::after {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 12px;
  height: 12px;
  margin-left: 10px;
  border: 2px solid #1f2d2e;
  border-radius: 50%;
}

.alt-nav--text {
  display: none;
  /* background-color: @bg-color;
		border-radius: 30px; */
}

.about-us {
  display: flex;
  justify-content: center;
  text-align: center;
}

.about-us--container {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(75% - 30px);
}

.about-us--info {
  line-height: 1.5;
  margin-bottom: 140px;
}

.about-us--advantages {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.about-us--advantage {
  position: relative;
}

.about-us--advantage::before {
  content: '';
  position: absolute;
  bottom: calc(160%);
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 60px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.about-us--advantage__favorably::before {
  background-image: url(../img/recommended.svg);
}

.about-us--advantage__qualitatively::before {
  background-image: url(../img/medal.svg);
}

.about-us--advantage__safely::before {
  background-image: url(../img/shield.svg);
}

.about-us--advantage__always::before {
  background-image: url(../img/24-hours-phone.svg);
}

.contacts {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(75% - 30px);
  margin: 0 auto;
  text-align: center;
  margin-bottom: 40px;
}

.contacts--wrap {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.contacts--map {
  width: calc(58.333333333333336% - 30px);
  display: inline-block;
  margin-right: 40px;
  border-radius: 15px;
  overflow: hidden;
}

.contacts--frame {
  width: 100%;
  height: 320px;
  display: block;
  border: none;
}

.contacts--list {
  display: inline-block;
  text-align: left;
}

.contacts--item {
  margin-bottom: 15px;
}

.contacts--item:hover .contacts--link,
.contacts--item:active .contacts--link {
  color: #48696b;
}

.contacts--item:hover .contacts--link__address::before,
.contacts--item:active .contacts--link__address::before {
  background-image: url(../img/address-active.svg);
}

.contacts--item:hover .contacts--link__phone::before,
.contacts--item:active .contacts--link__phone::before {
  background-image: url(../img/phone-active.svg);
}

.contacts--item:hover .contacts--link__email::before,
.contacts--item:active .contacts--link__email::before {
  background-image: url(../img/email-active.svg);
}

.contacts--item:hover .contacts--link__inst::before,
.contacts--item:active .contacts--link__inst::before {
  background-image: url(../img/instagram-active.svg);
}

.contacts--item:hover .contacts--link__fb::before,
.contacts--item:active .contacts--link__fb::before {
  background-image: url(../img/facebook-active.svg);
}

.contacts--link {
  display: flex;
  align-items: center;
}

.contacts--link::before {
  content: '';
  display: inline-block;
  margin-right: 7px;
  width: 20px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.contacts--link__address::before {
  background-image: url(../img/address.svg);
}

.contacts--link__phone::before {
  background-image: url(../img/phone.svg);
}

.contacts--link__email::before {
  background-image: url(../img/email.svg);
}

.contacts--link__inst::before {
  background-image: url(../img/instagram.svg);
}

.contacts--link__fb::before {
  background-image: url(../img/facebook.svg);
}

.orders {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  width: 45%;
  max-height: 100%;
  min-height: 100%;
  background-color: #eafdf8;
  font-size: 18px;
  text-align: center;
  overflow-y: auto;
  z-index: 2;
  transition: all 1s ease;
}

.orders__show {
  right: 0;
}

.orders__hide {
  right: -45%;
}

.orders--close {
  position: absolute;
  top: 25px;
  left: 10px;
  border: none;
  background: none;
  padding: 0;
  font-size: 0;
  width: 25px;
  height: 25px;
  background: url(../../img/arrow.svg) center no-repeat;
  background-size: contain;
  transform: rotate(0deg);
  transition: transform 0.8s ease;
}

.orders--close__rotate {
  transform: rotate(180deg);
}

.orders--header {
  padding: 20px 0;
  background-color: #1f2d2e;
  font-size: 35px;
  color: #eafdf8;
  text-align: center;
  margin-bottom: 20px;
}

.orders--list {
  margin-bottom: 15px;
}

.orders--item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 10px;
  margin-bottom: 5px;
}

.orders--input {
  width: 80px;
  padding: 2px 3px;
  border: none;
  border: 1px solid #1f2d2e;
  border-radius: 5px;
  text-align: center;
}

.orders--input__name {
  width: 150px;
}

.orders--input__surname {
  width: 150px;
}

.orders--input__phone {
  width: 190px;
}

.orders--field {
  margin-bottom: 10px;
}

.orders--submit {
  margin-top: 10px;
  margin-bottom: 10px;
  border: none;
  border-radius: 7px;
  background-color: #1f2d2e;
  color: #eafdf8;
  font-size: 20px;
  padding: 6px 15px;
}

.orders--submit:hover {
  background-color: #2d4243;
}

.orders--submit:active {
  background-color: #111819;
}

.order--img {
  max-width: 90px;
}

.order--amount {
  background-color: #eafdf8;
  width: 32px;
  padding: 2px 0;
  padding-left: 5px;
  border: none;
  border-radius: 5px;
  outline: none;
}

.order--amount::-webkit-inner-spin-button,
.order--amount::-webkit-outer-spin-button {
  opacity: 1;
}

.order--info {
  position: relative;
  margin-left: 10px;
  margin-right: 10px;
  text-align: left;
}

.order--delete {
  font-size: 0;
  border: none;
  background: none;
  padding: 0;
  position: absolute;
  top: 50%;
  right: -30px;
  transform: translateY(-50%);
  width: 12px;
  height: 12px;
  background: url(../../img/delete.svg) center no-repeat;
  background-size: contain;
}

.price {
  margin-bottom: 20px;
  font-size: 22px;
}

.hidden {
  display: none;
}

.thanks-for-order {
  padding: 30px;
  background-color: #d7e0ca;
  text-align: center;
  line-height: 1.6;
  border-radius: 10px;
}

.thanks-for-order--btn {
  font-size: 0;
  border: none;
  background: none;
  padding: 0;
  position: absolute;
  top: -10px;
  right: -20px;
  width: 17px;
  height: 17px;
  background: url(../../img/delete.svg) center no-repeat;
  background-size: contain;
}

@media screen and (max-width: 1400px) {
  .alt-nav--item__active .alt-nav--text {
    display: none;
  }
}

@media screen and (max-width: 1270px) {
  .about-us--container {
    width: calc(91.66666666666667% - 30px);
  }

  .contacts {
    width: calc(91.66666666666667% - 30px);
  }

  .contacts--map {
    width: calc(50% - 30px);
  }

  .contacts--frame {
    height: 300px;
  }
}

@media screen and (max-width: 992px) {
  .header {
    margin-bottom: 35px;
  }

  .main-header--container {
    padding: 0;
  }

  .main-header--nav {
    width: auto;
    margin-right: auto;
  }

  .main-header--list {
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #446655;
    overflow: hidden;
    transition: all 1.5s ease;
  }

  .main-header--list__show {
    max-height: 100vh;
  }

  .main-header--list__hide {
    max-height: 0;
  }

  .main-header--item {
    display: block;
    font-size: 23px;
    text-align: center;
    border: 2px solid rgba(210, 249, 249, 0.7);
    border-bottom: none;
  }

  .main-header--item:hover .main-header--link,
  .main-header--item:focus .main-header--link {
    background-color: rgba(31, 45, 46, 0.4);
  }

  .main-header--item:active .main-header--link {
    background-color: rgba(31, 45, 46, 0.55);
  }

  .main-header--item:last-child {
    border-bottom: 2px solid rgba(210, 249, 249, 0.7);
  }

  .main-header--link {
    padding: 20px 30px;
  }

  .main-header--mobile {
    display: block;
  }

  .shop-nav--container {
    width: calc(83.33333333333334% - 30px);
  }

  .shop--item {
    width: calc(33.333333333333336% - 30px);
  }

  .about-us--container {
    width: calc(100% - 30px);
  }

  .contacts {
    width: calc(100% - 30px);
  }

  .contacts--map {
    width: calc(58.333333333333336% - 30px);
  }

  .contacts--map {
    margin-right: 30px;
  }

  .contacts--frame {
    height: 280px;
  }

  .contacts--link {
    font-size: 16px;
  }

  .contacts--link::before {
    width: 16px;
    height: 16px;
    margin-right: 5px;
  }
}

@media screen and (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .header {
    margin-bottom: 15px;
    letter-spacing: 4px;
  }

  .main-header--container {
    padding: 0;
  }

  .main-header--amount {
    margin-left: 3px;
    padding: 1px 6px;
    font-size: 16px;
  }

  .main-header--buy::before {
    width: 33px;
    height: 31px;
  }

  .shop-nav--container {
    padding: 10px 0;
  }

  .shop-nav--header {
    margin-bottom: 15px;
    letter-spacing: 4px;
  }

  .shop-nav--item {
    flex-basis: 50%;
    margin-bottom: 12px;
  }

  .shop--item {
    width: calc(50% - 30px);
  }

  .product--color {
    width: 22px;
    height: 22px;
  }

  .alt-nav {
    display: none;
  }

  .about-us--info {
    margin-bottom: 120px;
  }

  .about-us--advantage::before {
    width: 50px;
